//@fileoverview Component that generates the section to show financial templates. All resources must redirect to contact form

import React from 'react';

//Function that creates and returns financial templates. Each template is returned in bootstrap card format
//@return Templates UI in html format
const Template = () => {
  return (
    <div>
        <section id="contenedorPlantillas" className="container">
            <div className="row">
                <div className="col-md-12 text-center">
                <span className="titulo2">Plantillas para la administracin de tus finanzas</span>
                </div>
            </div>
            <div className="row rowSeparationSm">
                <div className="col-md-6 col-lg-3">
                <div className="card text-center noBorder">            
                    <div className="card-body padding2">
                        <div className='cardInner'>                        
                            <span className="card-title bold titulo4">Presupuesto inicial de emprendimiento</span>
                            <p className="card-text justifyText">Listar las fuentes de financiamiento (inversores, prstamos y otros) y costos estimados (por nica vez y mensuales).</p>
                            <a href="#contactContainer" aria-label="Presupuesto inicial de emprendimiento" className="btn btn-success">Descargar gratis <i className="fa-solid fa-download"></i></a>
                        </div>
                    </div>            
                </div>                    

                </div>
                <div className="col-md-6 col-lg-3">
                <div className="card text-center noBorder">
                    <div className="card-body padding2">
                        <div className='cardInner'>
                            <span className="card-title bold titulo4">Control de gastos</span>
                            <p className="card-text justifyText">Registro detallado de todos tus gastos, clasificados por categoras y subcategoras. Obten totales y porcentaje de gasto con apertura por categora.</p>
                            <a href="#contactContainer"  aria-label="Control de gastos" className="btn btn-success">Descargar gratis <i className="fa-solid fa-download"></i></a>
                        </div>
                    </div>            
                </div>
                </div>
                <div className="col-md-6 col-lg-3">
                <div className="card text-center noBorder">
                    <div className="card-body padding2">
                        <div className='cardInner'>
                            <span className="card-title bold titulo4">Presupuesto de gastos personales</span>
                            <p className="card-text justifyText">Informes mensuales y por categora que ayuda a establecer un presupuesto mensual del gasto y hacer un seguimiento.</p>
                            <a href="#contactContainer" aria-label="Presupuesto de gastos personales" className="btn btn-success btnPlantilla"></a>
                        </div>
                    </div>            
                </div>
                </div>
                <div className="col-md-6 col-lg-3">
                <div className="card text-center noBorder">
                    <div className="card-body padding2">
                        <div className='cardInner'>
                            <span className="card-title bold titulo4">Diagrama de Gantt</span>
                            <p className="card-text justifyText">Control de las distintas fases del proyecto y su porcentaje de avance de manera dinmica y a nivel semanal, establece el inicio y duracin en terminos de cada fase.</p>
                            <a href="#contactContainer" aria-label="Diagrama de Gantt" className="btn btn-success">Descargar gratis <i className="fa-solid fa-download"></i></a>
                        </div>
                    </div>            
                </div>
                </div>
            </div>
            <div className="row rowSeparationSm">
                <div className="col-md-6 col-lg-3">
                <div className="card text-center noBorder">
                    <div className="card-body padding2">
                        <div className='cardInner'>
                            <span className="card-title bold titulo4">Informe de ventas bsico</span>
                            <p className="card-text justifyText">Organiza tus ventas en periodos trimestrales para un control an ms preciso y organizado para llevar la contabilidad de tu negocio.</p>
                            <a href="#contactContainer" aria-label="Informe de ventas bsico" className="btn btn-success btnPlantilla"></a>
                        </div>
                    </div>            
                </div>
                </div>
                <div className="col-md-6 col-lg-3">          
                    <div className="card text-center noBorder">
                    <div className="card-body padding2">
                        <div className='cardInner'>
                                <span className="card-title bold titulo4">Dashboard de cobranzas</span>
                                <p className="card-text justifyText">Diseada para ayudarte a administrar de manera efectiva las facturas por cobrar de tus clientes y el estado de tu de facturacin.</p>
                                <a href="#contactContainer" aria-label="Dashboard de cobranzas" className="btn btn-success btnPlantilla1"></a>
                        </div>
                    </div>            
                    </div>          
                </div>
                <div className="col-md-6 col-lg-3">          
                    <div className="card text-center noBorder">
                    <div className="card-body padding2">
                        <div className='cardInner'>
                                <span className="card-title bold titulo4">Anlisis de la competencia</span>
                                <p className="card-text justifyText">Estudio de mercado para organizar toda la informacin de tu competencia. Explora el panorama y localiza los puntos de crecimiento.</p>
                                <a href="#contactContainer" aria-label="Anlisis de la competencia" className="btn btn-success">Descargar gratis <i className="fa-solid fa-download"></i></a>
                        </div>
                    </div>            
                    </div>        
                </div>
                <div className="col-md-6 col-lg-3">
                <div className="card text-center noBorder">
                    <div className="card-body padding2">
                        <div className='cardInner'>
                            <span className="card-title bold titulo4">Presupuesto de Marketing</span>
                            <p className="card-text justifyText">Seguimiento del gasto en marketing, identificando lo que queda de presupuesto a gastar en lo que queda del ao.</p>
                            <a href="#contactContainer" aria-label="Presupuesto de Marketing" className="btn btn-success">Descargar gratis <i className="fa-solid fa-download"></i></a>
                        </div>
                    </div>            
                </div>
                </div>
            </div>
            </section>
    </div>
  );
}

export default Template;